import React from 'react';
import { connect } from 'react-redux';
import '../css/indexGame.css';
import 'antd/dist/antd.css';
import { message } from 'antd';

class Checkers extends React.Component {

    transferMove = (props) => { // фун-я проверяет поле, и записывает возможные ходы перед началом хода каждого из играков
        let result = this.checkAllCheckers([props[0], props[1]]); //проверка всех шашек для определенного игрока
        if (result[0].length !== 0) {// если обязательный бой есть
            this.props.onChangeOnlyClickFirst(result);
            this.props.onChangeBattleIs(true);
        } else if (result[1] === false) {
            message.success('Конец игры, ' + props[1] + ' игрок проиграл!');
        }
    }

    handleClick = (i, j) => {
        let tempGrid = JSON.parse(JSON.stringify(this.props.array)), checker = tempGrid[i][j].checkerType, colorReproduced = this.props.colorReproduced,
            flagChose = ((checker === 'whiteType' || checker === 'whiteQeen') && colorReproduced === 'white') || ((checker === 'blackType' || checker === 'blackQeen') && colorReproduced === 'black');
        if (tempGrid[i][j].checkerType !== undefined) {
            let result = this.calculationMovesChecker([tempGrid, i, j]);
            if (this.props.pressToMove.cutOut.length === 0) {//первый клик
                if (flagChose) {
                    if (this.props.battleIs) {// если есть обязательный бой
                        let flag = result.tempForBattle.length !== 0;
                        if (flag) {
                            result.tempForBattle.forEach(item => {
                                if (item.whoBattle[0] === +i && item.whoBattle[1] === +j) {
                                    // console.log(item);
                                    if (typeof (item.checkersMoveInThis[0]) === 'number') {
                                        tempGrid[item.checkersMoveInThis[0]][item.checkersMoveInThis[1]].click = 'checkerClick';
                                    } else {
                                        item.checkersMoveInThis.forEach(keys => {
                                            console.log('данные о бое', item.checkersMoveInThis, keys);
                                            tempGrid[keys[0]][keys[1]].click = 'checkerClick';
                                        })
                                    }
                                }
                            })
                            tempGrid[i][j].click = 'checkerClick';
                            this.props.onChangeSelectedCheckers([+i, +j]);
                            this.props.onChangeClickCheckers(this.filterObject(tempGrid));
                            this.saveChanges(tempGrid);

                            this.props.onChangePressToMove({
                                cutOut: [i, j],
                                insert: [],
                            })
                        } else {
                            message.error('Бой обязателен');
                        }
                    } else { // если обязательного боя нету
                        this.calculationPossibleMoves([tempGrid, i, j]);
                        this.props.onChangePressToMove({
                            cutOut: [i, j],
                            insert: []
                        })
                    }
                } else { // если кликнули не на шашку
                    message.error('Выбирите пожалуйста шашку своего цвета');
                }
            } else { //второй клик
                let flag = tempGrid[i][j].click === 'checkerClick';
                if (!flag && flagChose) {
                    if (this.props.battleIs && result.tempForBattle.length !== 0) {
                        for (let key in tempGrid) {
                            for (let key2 in tempGrid[key]) {
                                if (tempGrid[key][key2].color !== 'white') {
                                    tempGrid[key][key2].click = '';
                                }
                            }
                        }
                        result.tempForBattle.forEach(item => {
                            if (item.whoBattle[0] === +i && item.whoBattle[1] === +j) {
                                if (typeof (item.checkersMoveInThis[0]) === 'number') {
                                    tempGrid[item.checkersMoveInThis[0]][item.checkersMoveInThis[1]].click = 'checkerClick';
                                } else {
                                    item.checkersMoveInThis.forEach(keys => {
                                        tempGrid[keys[0]][keys[1]].click = 'checkerClick';
                                    })
                                }
                            }
                        })
                        tempGrid[i][j].click = 'checkerClick';
                        this.props.onChangeSelectedCheckers([+i, +j]);
                        this.props.onChangeClickCheckers(this.filterObject(tempGrid));
                        this.saveChanges(tempGrid);

                        this.props.onChangePressToMove({
                            cutOut: [i, j],
                            insert: [],
                        })
                    } else if (!(this.props.battleIs)) {
                        this.calculationPossibleMoves([tempGrid, i, j]);
                        this.props.onChangePressToMove({
                            cutOut: [i, j],
                            insert: []
                        })
                    }
                } else if (flag) {
                    let result2 = this.calculationMovesChecker([tempGrid, this.props.pressToMove.cutOut[0], this.props.pressToMove.cutOut[1]]);
                    if (this.props.pressToMove.cutOut[0] !== i && this.props.pressToMove.cutOut[1] !== j) {
                        this.moveCheckers({//вызываем функию передавая координату куда переместить шашку
                            cutOut: this.props.pressToMove.cutOut,
                            insert: [i, j],
                            infoBattle: result2.tempForBattle
                        });
                    }
                } else if (!flag) {
                    message.error('Вы нажали не туда');
                }
            }
        }
    }

    saveChanges = (props) => {
        this.props.onChangeGridArray(props);
        this.renderGrid(props);
    }

    setDiagonals = (props) => {
        //  ('diago');
        //  (props);
        let tempGrid = JSON.parse(JSON.stringify(props)), diagonals = this.props.diagonals;
        for (let key in tempGrid) {
            for (let key2 in tempGrid[key]) {
                if (tempGrid[key][key2].checkerType !== 'noneChecker' && tempGrid[key][key2].checkerType !== 'none') {
                    // if (tempGrid[key][key2].checkerType === 'whiteType' || tempGrid[key][key2].checkerType === 'blackType') {
                    tempGrid[key][key2].onDiagonals = [];
                    for (let diag in diagonals) {
                        diagonals[diag].forEach(keys => {
                            if (keys[0] === +key && keys[1] === +key2) {
                                tempGrid[key][key2].onDiagonals.push(diag);
                            }
                        });
                    }
                    // }
                }
            }
        }
        this.saveChanges(tempGrid);
    }

    moveCheckers = (props) => {
        //получаем данные о шашке, откуда и куда переместить
        let tempGrid = JSON.parse(JSON.stringify(this.props.array)), x1 = +props.cutOut[0], x2 = +props.insert[0],
            y1 = +props.cutOut[1], y2 = +props.insert[1];
        let flag = false;
        //убираем шашку которая была побита
        let infoBattle = props.infoBattle;
        console.log('battle in this', infoBattle);
        for (let key in infoBattle) {
            console.log(typeof (infoBattle[key].checkersMoveInThis[0]));
            if (typeof (infoBattle[key].checkersMoveInThis[0]) === 'number') {
                if (infoBattle[key].checkersMoveInThis[0] === x2 && infoBattle[key].checkersMoveInThis[1] === y2) {
                    tempGrid[infoBattle[key].checkersLose[0]][infoBattle[key].checkersLose[1]].checkerType = 'none';
                    flag = true;
                }
            } else {
                infoBattle[key].checkersMoveInThis.forEach(item => {
                    if (item[0] === x2 && item[1] === y2) {
                        tempGrid[infoBattle[key].checkersLose[0]][infoBattle[key].checkersLose[1]].checkerType = 'none';
                        flag = true;
                    }
                })
            }
            infoBattle[key].checkersMoveInThis.forEach(item => {
                if (item[0] === x2 && item[1] === y2) {
                    tempGrid[infoBattle[key].checkersLose[0]][infoBattle[key].checkersLose[1]].checkerType = 'none';
                    flag = true;
                }
            })

        }
        //очищаем выдиление
        for (let key in tempGrid) {
            for (let key2 in tempGrid[key]) {
                if (tempGrid[key][key2].color !== 'white') {
                    tempGrid[key][key2].click = '';
                }
            }
        }
        //перемещаем шашку
        // console.log('move', tempGrid[x2][y2], x2);
        if (tempGrid[x1][y1].checkerType === 'whiteType' && x2 === 3) {
            tempGrid[x2][y2].checkerType = 'whiteQeen';
        } else if (tempGrid[x1][y1].checkerType === 'blackType' && x2 === 10) {
            tempGrid[x2][y2].checkerType = 'blackQeen';
        } else {
            tempGrid[x2][y2].checkerType = tempGrid[x1][y1].checkerType;
        }
        tempGrid[x1][y1].checkerType = 'none';
        //если был бой
        if (flag) {
            let result = this.calculationMovesChecker([tempGrid, x2, y2]);
            if (result.tempForBattle.length === 0) {//если для шашки котора ходила, нету возможности еще раз бить, передаем ход
                this.props.onChangeColorReproduced((this.props.colorReproduced === 'white') ? 'black' : 'white');
                this.props.onChangeInfoCheckersForBattle([]);
                this.props.onChangeHistoryArray([...this.props.historyArray, tempGrid]);
                //очищаем масив передвижений
                this.props.onChangePressToMove({
                    cutOut: [],
                    insert: []
                });
                this.props.onChangeBattleIs(false);
                this.transferMove([tempGrid, (this.props.colorReproduced === 'white') ? 'black' : 'white']);
            } else { // если возможность бить есть, то делаем второй ход
                this.props.onChangePressToMove({
                    cutOut: [x2, y2],
                    insert: []
                });
                this.props.onChangeSelectedCheckers([x2, y2]);
            }
        } else { // если боя небыло передаем ход
            this.props.onChangeColorReproduced((this.props.colorReproduced === 'white') ? 'black' : 'white');
            this.props.onChangeInfoCheckersForBattle([]);
            this.props.onChangeHistoryArray([...this.props.historyArray, tempGrid]);
            //очищаем масив передвижений
            this.props.onChangePressToMove({
                cutOut: [],
                insert: []
            });
            this.props.onChangeBattleIs(false);
            this.transferMove([tempGrid, (this.props.colorReproduced === 'white') ? 'black' : 'white']);
        }
        this.saveChanges(tempGrid);
        this.setDiagonals(tempGrid);
    }

    filterObject = (object) => {
        var result = [], key1, key2;
        for (key1 in object) {
            for (key2 in object[key1]) {
                if (object[key1][key2].click === 'checkerClick')
                    result.push([key1, key2]);
            }
        }
        return result;
    }

    calculationMovesChecker = (props) => {//функция выщитывает возможные ходы для выбраной шашки
        let tempGrid = JSON.parse(JSON.stringify(props[0])),
            i = +props[1],
            j = +props[2],
            tempMain = [],
            tempForBattle = [],
            flag = false,
            x = i,
            y = j,
            colorCheckers = tempGrid[i][j].checkerType,
            colorBattleCheckers = (colorCheckers === 'blackQeen' || colorCheckers === 'blackType') ? ['whiteType', 'whiteQeen'] : ['blackType', 'blackQeen'];

        for (let key in tempGrid) {
            for (let key2 in tempGrid[key]) {
                if (tempGrid[key][key2].color !== 'white') {//если цвет клетки не белый, то очистить нажатие
                    tempGrid[key][key2].click = '';
                }
            }
        }

        if (colorCheckers === 'blackQeen' || colorCheckers === 'whiteQeen') {
            for (var iterat = 1; iterat <= 4; iterat++) {
                let temp = [];
                switch (iterat) {
                    case 1: { //верхний правый
                        x -= 1;
                        y += 1;
                        while (tempGrid[x][y].checkerType !== 'noneChecker') {
                            if (tempGrid[x][y].checkerType === 'none') {
                                temp.push([x, y]);
                            } else if (tempGrid[x][y].checkerType === colorBattleCheckers[0] || tempGrid[x][y].checkerType === colorBattleCheckers[1]) {
                                if (tempGrid[x - 1][y + 1].checkerType === 'none') {
                                    let x2 = x - 2, y2 = y + 2, MoveInThisForBattle = [[x - 1, y + 1]];
                                    while (tempGrid[x2][y2].checkerType !== 'noneChecker') {
                                        if (tempGrid[x2][y2].checkerType === 'none') {
                                            MoveInThisForBattle.push([x2, y2]);
                                        } else {
                                            break;
                                        }
                                        x2 -= 1;
                                        y2 += 1;
                                    }
                                    tempForBattle.push({
                                        whoBattle: [i, j],
                                        checkersLose: [x, y],
                                        checkersMoveInThis: MoveInThisForBattle
                                    });
                                } else {
                                    break;
                                }
                            } else {
                                break;
                            }

                            x -= 1;
                            y += 1;
                        }
                        break;
                    }
                    case 2: { //верхний левый
                        x -= 1;
                        y -= 1;
                        while (tempGrid[x][y].checkerType !== 'noneChecker') {
                            if (tempGrid[x][y].checkerType === 'none') {
                                temp.push([x, y]);
                            } else if (tempGrid[x][y].checkerType === colorBattleCheckers[0] || tempGrid[x][y].checkerType === colorBattleCheckers[1]) {
                                if (tempGrid[x - 1][y - 1].checkerType === 'none') {
                                    let x2 = x - 2, y2 = y - 2, MoveInThisForBattle = [[x - 1, y - 1]];
                                    while (tempGrid[x2][y2].checkerType !== 'noneChecker') {
                                        if (tempGrid[x2][y2].checkerType === 'none') {
                                            MoveInThisForBattle.push([x2, y2]);
                                        } else {
                                            break;
                                        }
                                        x2 -= 1;
                                        y2 -= 1;
                                    }
                                    tempForBattle.push({
                                        whoBattle: [i, j],
                                        checkersLose: [x, y],
                                        checkersMoveInThis: MoveInThisForBattle
                                    });
                                } else {
                                    break;
                                }
                            } else {
                                break;
                            }
                            x -= 1;
                            y -= 1;
                        }

                        break;
                    }
                    case 3: { //нижний правый
                        x += 1;
                        y += 1;
                        while (tempGrid[x][y].checkerType !== 'noneChecker') {
                            if (tempGrid[x][y].checkerType === 'none') {
                                temp.push([x, y]);
                            } else if (tempGrid[x][y].checkerType === colorBattleCheckers[0] || tempGrid[x][y].checkerType === colorBattleCheckers[1]) {
                                if (tempGrid[x + 1][y + 1].checkerType === 'none') {
                                    let x2 = x + 2, y2 = y + 2, MoveInThisForBattle = [[x + 1, y + 1]];
                                    while (tempGrid[x2][y2].checkerType !== 'noneChecker') {
                                        if (tempGrid[x2][y2].checkerType === 'none') {
                                            MoveInThisForBattle.push([x2, y2]);
                                        } else {
                                            break;
                                        }
                                        x2 += 1;
                                        y2 += 1;
                                    }
                                    tempForBattle.push({
                                        whoBattle: [i, j],
                                        checkersLose: [x, y],
                                        checkersMoveInThis: MoveInThisForBattle
                                    });
                                } else {
                                    break;
                                }
                            } else {
                                break;
                            }
                            x += 1;
                            y += 1;
                        }

                        break;
                    }
                    case 4: { //нижний левый
                        x += 1;
                        y -= 1;
                        while (tempGrid[x][y].checkerType !== 'noneChecker') {
                            if (tempGrid[x][y].checkerType === 'none') {
                                temp.push([x, y]);
                            } else if (tempGrid[x][y].checkerType === colorBattleCheckers[0] || tempGrid[x][y].checkerType === colorBattleCheckers[1]) {
                                if (tempGrid[x + 1][y - 1].checkerType === 'none') {
                                    let x2 = x + 2, y2 = y - 2, MoveInThisForBattle = [[x + 1, y - 1]];
                                    while (tempGrid[x2][y2].checkerType !== 'noneChecker') {
                                        if (tempGrid[x2][y2].checkerType === 'none') {
                                            MoveInThisForBattle.push([x2, y2]);
                                        } else {
                                            break;
                                        }
                                        x2 += 1;
                                        y2 -= 1;
                                    }
                                    tempForBattle.push({
                                        whoBattle: [i, j],
                                        checkersLose: [x, y],
                                        checkersMoveInThis: MoveInThisForBattle
                                    });
                                } else {
                                    break;
                                }
                            } else {
                                break;
                            }

                            x += 1;
                            y -= 1;
                        }

                        break;
                    }
                    default:
                        continue;
                }
                flag = false;
                x = i;
                y = j;
                temp.forEach(key3 => { tempMain.push(key3) });
            }

        } else {
            tempGrid[i][j].onDiagonals.forEach(key => {//выбираем диагонали на которых стоит шашка
                let temp = [];
                if (colorCheckers === 'blackType' || colorCheckers === 'whiteType') {

                    temp = this.props.diagonals[key].filter(item => {//скорочуємо до 2 кліток з різних боків, для звичайних шашок
                        if (Math.abs(item[0] - i) <= 2 && Math.abs(item[1] - j) <= 2)
                            return true;
                        return false;
                    })

                    temp = temp.filter(item => { //прибираємо всі місця де вже є шашки
                        if (tempGrid[item[0]][item[1]].checkerType === 'none')
                            return true;
                        return false;
                    });
                    //  (temp);
                    temp = temp.filter(item => {// проходим по координатам диагоналей, проверяя их как потенциальное место для хода
                        const I = item[0], J = item[1];
                        if (I === i && J === j) {
                            return false;
                        } else if (I === (i - 1) && J === (j - 1)) {
                            if (colorCheckers === 'whiteType') {
                                if ((tempGrid[I][J].checkerType === 'blackType' || tempGrid[I][J].checkerType === 'blackQeen') && tempGrid[I - 1][J - 1].checkerType === 'none') {
                                    return false;
                                } else {
                                    return true;
                                }
                            } else {
                                return false;
                            }

                        } else if (I === (i - 1) && J === (j + 1)) {
                            if (colorCheckers === 'whiteType') {
                                if ((tempGrid[I][J].checkerType === 'blackType' || tempGrid[I][J].checkerType === 'blackQeen') && tempGrid[I - 1][J + 1].checkerType === 'none') {
                                    return false;
                                } else {
                                    return true;
                                }
                            } else {
                                return false;
                            }

                        } else if (I === (i + 1) && J === (j + 1)) {
                            if (colorCheckers === 'blackType') {
                                if ((tempGrid[I][J].checkerType === 'whiteType' || tempGrid[I][J].checkerType === 'whiteQeen') && tempGrid[I + 1][J + 1].checkerType === 'none') {
                                    return false;
                                } else {
                                    return true;
                                }
                            } else {
                                return false;
                            }
                        } else if (I === (i + 1) && J === (j - 1)) {
                            if (colorCheckers === 'blackType') {
                                if ((tempGrid[I][J].checkerType === 'whiteType' || tempGrid[I][J].checkerType === 'whiteQeen') && tempGrid[I + 1][J - 1].checkerType === 'none') {
                                    // tempForWhite.push(item);
                                    return false;
                                } else {
                                    return true;
                                }
                            } else {
                                return false;
                            }
                        } else if (I === (i - 2) && J === (j - 2)) {
                            if (tempGrid[I][J].checkerType === 'none' && (tempGrid[I + 1][J + 1].checkerType === colorBattleCheckers[0] || tempGrid[I + 1][J + 1].checkerType === colorBattleCheckers[1])) {
                                tempForBattle.push({
                                    whoBattle: [i, j],
                                    checkersLose: [I + 1, J + 1],
                                    checkersMoveInThis: [I, J]
                                });
                                return true;
                            } else {
                                return false;
                            }
                        } else if (I === (i - 2) && J === (j + 2)) {
                            if (tempGrid[I][J].checkerType === 'none' && (tempGrid[I + 1][J - 1].checkerType === colorBattleCheckers[0] || tempGrid[I + 1][J - 1].checkerType === colorBattleCheckers[1])) {
                                tempForBattle.push({
                                    whoBattle: [i, j],
                                    checkersLose: [I + 1, J - 1],
                                    checkersMoveInThis: [I, J]
                                });
                                return true;
                            } else {
                                return false;
                            }
                        } else if (I === (i + 2) && J === (j + 2)) {
                            if (tempGrid[I][J].checkerType === 'none' && (tempGrid[I - 1][J - 1].checkerType === colorBattleCheckers[0] || tempGrid[I - 1][J - 1].checkerType === colorBattleCheckers[1])) {
                                tempForBattle.push({
                                    whoBattle: [i, j],
                                    checkersLose: [I - 1, J - 1],
                                    checkersMoveInThis: [I, J]
                                });
                                return true;
                            } else {
                                return false;
                            }
                        } else if (I === (i + 2) && J === (j - 2)) {
                            if (tempGrid[I][J].checkerType === 'none' && (tempGrid[I - 1][J + 1].checkerType === colorBattleCheckers[0] || tempGrid[I - 1][J + 1].checkerType === colorBattleCheckers[1])) {
                                tempForBattle.push({
                                    whoBattle: [i, j],
                                    checkersLose: [I - 1, J + 1],
                                    checkersMoveInThis: [I, J]
                                });
                                return true;
                            } else {
                                return false;
                            }
                        }
                        return false;
                    });
                }
                temp.forEach(item => { tempMain.push(item) });

            })
        }
        return {
            temp: tempMain,
            tempForBattle,
            tempGrid
        };
    }

    checkAllCheckers = (props) => {
        let tempValue = [], flag = false;
        const tempGrid = props[0], color = (props[1] === 'white') ? ['whiteType', 'whiteQeen'] : ['blackType', 'blackQeen'];
        //  ('tempGrid', tempGrid, color);
        for (var key in tempGrid) {
            for (var key2 in tempGrid[key]) {
                if (tempGrid[key][key2].checkerType === color[0] || tempGrid[key][key2].checkerType === color[1]) {
                    let temp = this.calculationMovesChecker([tempGrid, key, key2]);
                    if (temp.temp.length !== 0) {
                        flag = true;
                    }
                    temp.tempForBattle.forEach(item => {
                        tempValue.push(item);
                    })
                }
            }
        }
        return [tempValue, flag];
    }

    calculationPossibleMoves = (props) => {//фун-я вычисления возможных ходов для выбраной шашки
        let result = this.calculationMovesChecker([props[0], props[1], props[2]]);
        let tempGrid = JSON.parse(JSON.stringify(result.tempGrid)), i = props[1], j = props[2];
        result.temp.forEach(keys => {
            tempGrid[keys[0]][keys[1]].click = 'checkerClick';
        })
        this.props.onChangeSelectedCheckers([+i, +j]);
        this.props.onChangeClickCheckers(this.filterObject(tempGrid));
        this.saveChanges(tempGrid);
        return result;
    }

    renderGrid = (props) => {
        var renderGrid = [];
        let tempGrid = JSON.parse(JSON.stringify(props));
        for (var key in tempGrid) {
            for (var key2 in tempGrid[key]) {
                if (tempGrid[key][key2].checkerType !== 'noneChecker') {
                    const i = key, j = key2;
                    renderGrid.push(<div
                        className={tempGrid[key][key2].color + " " + tempGrid[key][key2].checkerType + " " + tempGrid[key][key2].click}
                        key={key + " " + key2}
                        onClick={() => { this.handleClick(i, j) }}
                    />);
                }
            }
        }
        this.props.onChangeRenderArray(renderGrid);
    }

    componentWillMount() {
        this.setDiagonals(this.props.array);
        this.renderGrid(this.props.array);
    }
    goToHitory = (props) => {
        // console.log(props);
        this.saveChanges(props);
    }
    render() {
        return (
            <div className='gridCheckers'>
                <div className="someText">
                    Checkers<br />
                    Ход : {this.props.colorReproduced}
                </div>
                <div className = 'mainBody'>
                    <div className='centerAlign'>
                        {this.props.renderGrid}
                    </div>
                    <div className = 'historyAlign'>
                        {this.props.historyArray.map((item, index) => {
                            return (<div key = {index} className = 'historyItem' onClick = {() => this.goToHitory(item)}>#{index}</div>)
                        })}
                    </div>
                </div>
                <div className='someText'>
                    Developed by Adm1ral_den
                </div>
            </div>
        );
    }

}

export default connect(

    state => ({
        array: state.grid.array,
        renderGrid: state.grid.renderGrid,
        selectedCheckers: state.grid.selectCheckers,
        colorReproduced: state.grid.colorReproduced,
        pressToMove: state.grid.pressToMove,
        onlyClickThisCheckers: state.grid.onlyClickThisCheckers,
        diagonals: state.grid.diagonals,
        infoChekersForBattle: state.grid.infoChekersForBattle,
        battleIs: state.grid.battleIs,
        onlyClickFirstCheckersInThis: state.grid.onlyClickFirstCheckersInThis,
        historyArray: state.grid.historyArray
    }),

    dispatch => ({
        onChangeHistoryArray: (value) => {
            dispatch({ type: 'CHANGE_HISTORY_ARRAY', array: value });
        },
        onChangeOnlyClickFirst: (value) => {
            dispatch({ type: 'CHANGE_ONLY_CLICK_FIRST', array: value });
        },
        onChangeBattleIs: (value) => {
            dispatch({ type: 'CHANGE_BATTLE_IS', value: value });
        },
        onChangeGridArray: (array) => {
            dispatch({ type: 'CHANGE_ARRAY_GRID', array: array });
        },
        onChangeRenderArray: (array) => {
            dispatch({ type: 'CHANGE_RENDER_GRID', array: array })
        },
        onChangeSelectedCheckers: (value) => {
            dispatch({ type: 'CHANGE_SELECTED_CHECKERS', value: value })
        },
        onChangePressToMove: (objectValue) => {
            dispatch({ type: 'CHANGE_PRESS_TO_MOVE', pressToMove: objectValue })
        },
        onChangeColorReproduced: (color) => {
            dispatch({ type: 'CHANGE_COLOR_REPRODUCED', colorReproduced: color })
        },
        onChangeClickCheckers: (object) => {
            dispatch({ type: 'CHANGE_CLICK_CHECKERS', objectCheckers: object })
        },
        onChangeInfoCheckersForBattle: (object) => {
            dispatch({ type: 'CHANGE_INFO_CHECKERS_FOR_BATTLE', infoChekersForBattle: object })
        },
    })

)(Checkers);