import React from 'react';
import 'antd/dist/antd.css';
import '../../App/css/registration.css';
import { connect } from 'react-redux';

import {
    Form,
    Input,
    Icon,
    message,
    Button,
  } from 'antd'; 
  class RegistrationForm extends React.Component {

    handleSubmit = e => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          let Reg = JSON.parse(localStorage.getItem("registraion"));
          if(!Reg){
            let temp = [];
            values.isOnline = false;
            delete values.confirm;
            temp.push(values);
            localStorage.setItem('registraion', JSON.stringify(temp));
            message.success('Пользователь успешно зарегестрирован');
            this.props.history.push('/login');
          }
          else{
            function isAlive(val){
              return val.email === values.email
            }
            if(!Reg.some(isAlive))
              {
              values.isOnline = false;
              delete values.confirm;
              Reg.push(values);
              localStorage.setItem('registraion', JSON.stringify(Reg));
              message.success('Пользователь успешно зарегестрирован');
              this.props.history.push('/login');
              }
            else {
              message.error('Пользователь уже зарегестрирован!');
            }
          }
        }
      });
    };
  
    handleConfirmBlur = e => {
      const value = e.target.value;
      this.props.onChangeConfirmDirty(this.props.confirmDirty || !!value)
      // this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    };
  
    compareToFirstPassword = (rule, value, callback) => {
      const form = this.props.form;
      if (value && value !== form.getFieldValue('password')) {
        callback('Two passwords that you enter is inconsistent!');
      } else {
        callback();
      }
    };
  
    validateToNextPassword = (rule, value, callback) => {
      const form = this.props.form;
      if (value && this.props.confirmDirty) {
        form.validateFields(['confirm'], { force: true });
      }
      callback();
    };

    handleBack = () => {
      this.props.history.goBack();
    }
    render() {
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
      return (
        <div className='content'>
          <div className="d1">Register</div>
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Form.Item label="E-mail">
            {getFieldDecorator('email', {
              rules: [
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your E-mail!',
                },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label="Password" hasFeedback>
            {getFieldDecorator('password', {
              rules: [
                {
                  required: true,
                  message: 'Please input your password!',
                },
                {
                  validator: this.validateToNextPassword,
                },
              ],
            })(<Input.Password />)}
          </Form.Item>
          <Form.Item label="Confirm Password" hasFeedback>
            {getFieldDecorator('confirm', {
              rules: [
                {
                  required: true,
                  message: 'Please confirm your password!',
                },
                {
                  validator: this.compareToFirstPassword,
                },
              ],
            })(<Input.Password onBlur={this.handleConfirmBlur} />)}
          </Form.Item>
          <Form.Item {...tailFormItemLayout} >
            <div className='button_align'>
            <Button type="primary" htmlType="submit">
              Register
            </Button>
            <Button type="danger" onClick={this.handleBack}>
              <Icon type="left" />
              Go back
            </Button>
            </div>
            
          </Form.Item>
        </Form>
        </div>
        
      );
    }
  }
  
  const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
  
  export default connect(
    state => ({
      confirmDirty: state.confirmDirty,
    }),
    dispatch => ({ 
      onChangeConfirmDirty: (confirmDirty) => {
        dispatch({type: 'CHANGE_CONFIRM_DIRTY', payload: confirmDirty});
      }
    })
  )(WrappedRegistrationForm);