function renderGrid () {
    var grid = {};
    for(var i = 1; i <=12; i++){
        grid[i] = {};
        for(var j = 1; j <=12; j++){
            grid[i][j] = {};
            if(i <=2 || i >= 11 || j <=2 || j>= 11){
                grid[i][j].checkerType = 'noneChecker';
            }else{
                if( (i + j) % 2 === 0){
                    grid[i][j].color = 'white';
                }else{
                    if( i <= 5){
                        grid[i][j].checkerType = 'blackType';
                    }
                    else if( i >=8){
                        grid[i][j].checkerType = 'whiteType';
                    }else{
                        grid[i][j].checkerType = 'none';
                    }
                    grid[i][j].color = 'black';
                    grid[i][j].click = ' ';
                    grid[i][j].onDiagonals = [];
                }
            }
           
        }
    }
    return grid;
}
const defaultValue = {
    array: renderGrid(),
    diagonals: {
        a1h8: [[10,3], [9,4], [8,5], [7,6], [6,7], [5,8], [4,9], [3,10]],
        c1h6: [[10,5], [9,6], [8,7], [7,8], [6,9], [5,10]],
        c1a3: [[10,5], [9,4], [8,3]],
        e1h4: [[10,7], [9,8], [8,9], [7,10]],
        e1a5: [[10,7], [9,6], [8,5], [7,4], [6,3]],
        g1h2: [[10,9], [9,10]],
        g1a2: [[10,9], [9,8], [8,7], [7,6], [6,5], [5,4], [4,3]],
        a3f8: [[8,3], [7,4], [6,5], [5,6], [4,7], [3,8]],
        a5d8: [[6,3], [5,4], [4,5], [3,6]],
        a7b8: [[4,3], [3,4]],
        h2b8: [[9,10], [8,9], [7,8], [6,7], [5,6], [4,5], [3,4]],
        h4d8: [[7,10], [6,9], [5,8], [4,7], [3,6]],
        h6f8: [[5,10], [4,9], [3,8]]

    },
    colorReproduced: 'white',
    pressToMove: {
        cutOut: [],
        insert : []
    },
    infoChekersForBattle: [],
    onlyClickThisCheckers: [],
    battleIs: false,
    onlyClickFirstCheckersInThis: [],
    historyArray: []
}
export default function arrayGrid(state = defaultValue, action) {
    if(action.type === 'CHANGE_ARRAY_GRID'){
        return {
            ...state,
            array: action.array
        }
    }
    if(action.type === 'CHANGE_HISTORY_ARRAY'){
        return {
            ...state,
            historyArray: action.array
        }
    }
    if(action.type === 'CHANGE_ONLY_CLICK_FIRST'){
        return {
            ...state,
            onlyClickFirstCheckersInThis: action.array
        }
    }
    if(action.type === 'CHANGE_BATTLE_IS'){
        return {
            ...state,
            battleIs: action.value
        }
    }
    if(action.type === 'CHANGE_INFO_CHECKERS_FOR_BATTLE'){
        return {
            ...state,
            infoChekersForBattle: action.infoChekersForBattle
        }
    }
    if(action.type === 'CHANGE_CLICK_CHECKERS'){
        return {
            ...state,
            onlyClickThisCheckers: action.objectCheckers
        }
    }
    if(action.type === 'CHANGE_COLOR_REPRODUCED'){
        return {
            ...state,
            colorReproduced: action.colorReproduced
        }
    }
    if(action.type === 'CHANGE_RENDER_GRID'){
        return{
            ...state,
            renderGrid: action.array
        }
    }
    if(action.type === 'CHANGE_SELECTED_CHECKERS'){
        // console.log(state.array[action.value[0]][action.value[1]].checkerType);
            return{
            ...state,
            selectCheckers: action.value
            } 
    }
    if(action.type === 'CHANGE_PRESS_TO_MOVE'){
        // console.log(state.array[action.value[0]][action.value[1]].checkerType);
            return{
            ...state,
            pressToMove: action.pressToMove,
            } 
    }
    return state;
}